.category-DatePicker {
  margin-left: 10px;
  margin-right: 10px;
}
.view-all {
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: nowrap;
}

.category-date {
  text-align: center;
  display: flex;
  flex-direction: row;
  /* justify-content: space-between;  */
}
.category {
  margin-top: 42px;
  width: 60vw;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: center;
  background-color: rgb(241, 241, 241);
}

.category-line {
  text-align: center;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
}

.header {
  text-align: center;
}

.carousel-container {
  margin: 20px;
  width: 60vw;
}

.selected-image {
  width: 100%;
  height: 500px;
  margin-bottom: 8px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel {
  position: relative;
}

.carousel__images {
  display: flex;
  max-width: 100%;
  overflow-x: hidden;
}

.carousel__image-selected {
  border: 3px solid #ffa700 !important;
}

.carousel__image {
  margin-right: 10px;
  height: 150px;
  min-width: 150px;
  border: 3px solid #ffa70000;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel__button {
  position: absolute;
  top: 40%;
}

.carousel__button-left {
  left: -50px;
}

.carousel__button-right {
  right: -50px;
}




.line-top {
  width: 60vw;
  height: 1px;
  border-top: 2px solid rgb(216, 216, 216);
  padding-bottom: 20px;
  margin-top:10px;
}