.Carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: rgb(241, 241, 241);
}

.post_img {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 60vw;
  padding-bottom: 40px;
}

.directions {
  padding-top:50px;
  padding-bottom: 20px;
  display: flex;
  flex: 1 1 auto;

  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: justify;
  width: 100%;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  align-items: flex-start;
  /* flex-wrap: nowrap; */
  align-content: stretch;
}

.info {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  -webkit-text-size-adjust: 100%;
  letter-spacing: 0.4px;
  font-family: rosario, source-han-sans-traditional, sans-serif;
  color: rgb(196, 12, 35);
  font-size: 12px;
  /* line-height: 1.33; */
  text-align: justify;
}

.user-mini{
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  font-size: 23px;
  font-weight: 700;
  font-family: merriweather, source-han-serif-tc, serif;
  letter-spacing: 0.2px;
  line-height: 1.43;
  color: rgb(74, 73, 73);

  align-items: center;
  justify-content: justify;
  text-align: center;
}


.user{
    padding: 0;
    margin: 0;
    font-size: 23px;
    font-weight: 700;
    font-family: merriweather, source-han-serif-tc, serif;
    letter-spacing: 0.2px;
    line-height: 1.43;
    color: rgb(74, 73, 73);
}

.time {
  color: rgb(74, 73, 73);
  font-size: 13px;
